import React, {useState, useContext, useEffect} from 'react'
import { Card, Form, Button, Image, Row, Col, Container } from 'react-bootstrap'
import GlobalContext from '../helpers/GlobalContext';
import { getRndInteger } from '../classes/utils';

export default props => {
    const {global_context, setGlobalContext} = useContext(GlobalContext)
    const game_btns = [
        <Button 
            variant="primary" 
            onClick={() => {
                update_textLog(global_context.i18n.t('aventura_continue_1'));
                add_button_to_button_group(game_btns[1], true)
                }}>{global_context.i18n.t('Levantar')}</Button>,
        <Button 
            variant="primary" 
            onClick={() => {
                update_textLog(global_context.i18n.t('aventura_continue_2'));
                const chance = getRndInteger(0,99)
                if(chance < 20 && !button_group_copy.includes(game_btns[2]) && event_states.event_countdown ===0) {
                    add_button_to_button_group(game_btns[2])
                    update_textLog(global_context.i18n.t('you_see_something'));
                }
                else {
                    remove_button_from_button_group(game_btns[2]);
                    if (event_states.event_countdown > 0) event_states.event_countdown--
                    else event_states.event_countdown = 2
                }
                }}>{global_context.i18n.t('Continuar')}</Button>,
        <Button 
            variant="primary" 
            onClick={() => {
                var updated = false;
                if (event_states.olhar_events.length === 0 || (event_states.olhar_events[2])) {
                    update_textLog(global_context.i18n.t('aventura_olhar_1'));
                    event_states.olhar_events.push(true)
                    updated = true
                }
                if (event_states.olhar_events[0] && !updated){
                    update_textLog(global_context.i18n.t('aventura_olhar_2'));
                    event_states.olhar_events[0] = false
                    event_states.olhar_events.push(true)
                    updated = true
                }
                if (event_states.olhar_events[1] && !updated){
                    update_textLog(global_context.i18n.t('aventura_olhar_3'));
                    event_states.olhar_events[1] = false
                    event_states.olhar_events.push(true)
                    updated = true
                }
                remove_button_from_button_group(game_btns[2])
            }}>{global_context.i18n.t('olhar')}</Button>,
    ]
    var button_group_copy = [game_btns[0]]
    var text_log_copy = [global_context.i18n.t('aventura_start')]
    var event_states = {
        olhar_events: [],
        event_countdown: 2,
    }
    const [text_log, setTextLog] = useState([global_context.i18n.t('aventura_start')])
    const [button_group, setButtonGroup] = useState([game_btns[0]])
    const textLog = React.createRef();

    const update_textLog = (new_line) => {
        const local_text_log = Array.from(text_log_copy)
        local_text_log.push(new_line)
        setTextLog(local_text_log)
        text_log_copy=local_text_log
    }

    const add_button_to_button_group = (new_button, clear=false) => {
        var local_button_group = [];
        if (!clear) {
            local_button_group = Array.from(button_group_copy)
        }
        if (!local_button_group.includes(new_button)) local_button_group.push(new_button);
        setButtonGroup(local_button_group)
        button_group_copy = local_button_group
    }

    const remove_button_from_button_group = (old_button) => {
        const local_button_group = Array.from(button_group_copy)

        if (local_button_group.includes(old_button)){
            local_button_group.splice(local_button_group.indexOf(old_button, 1));
            setButtonGroup(local_button_group)
            button_group_copy = local_button_group
        }
    }

    useEffect(() => {
        textLog.current.scrollTop = textLog.current.scrollHeight;
    })

    return <Card style={{ width: '80vw', color: "white", backgroundColor: "#092777cf" }}>
                <Card.Header variant="top">
                    <Form.Control 
                        ref={textLog}
                        as="textarea" 
                        rows="10"  
                        autocorrect="off"
                        spellcheck="false"
                        style={{ resize: 'none', backgroundColor: "#161616", color: "#0D0", border: "inset #0b2d88cf 15px" }} 
                        value={text_log.join('\n')}
                        onChange={() => {
                            this.textLog.current.scrollTop = this.textLog.current.scrollHeight;
                            console.log('this: ', this);
                        }}/>
                </Card.Header>
                <Card.Body>
                    <Card.Title style={{fontWeight: "bold"}}>Adventure Game</Card.Title>
                    <div className='aventura-game-button-div'>
                        {button_group}
                    </div>
                    <div className='aventura-game-button-div'>
                        <Button 
                            variant="primary" 
                            onClick={() => {
                                global_context.terminal.terminalInput.current.disabled = !global_context.terminal.terminalInput.current.disabled
                                // console.log('global_context.terminal: ', global_context.terminal);
                                }}>{global_context.i18n.t('Ligar/Desligar input do terminal')}</Button>
                    </div>
                    <div className='aventura-game-button-div' style={{visibility: "hidden"}}>
                        <Container fluid style={{ paddingLeft: 0, paddingRight: 0, maxWidth: "55vw" }}>
                            <Row xs={{span: 6}} style={{ paddingLeft: 0, paddingRight: 0, alignItems: "center", border: "medium ridge #404040" }}>
                                <Col style={{ paddingLeft: 0, paddingRight: 0}}>
                                    <Image src={process.env.PUBLIC_URL + "/game_icons/1 gold.png"} rounded style={{width: "40px", height: "40px", margin: 0}}/>
                                </Col>
                                <Col style={{ paddingLeft: 0, paddingRight: 0 }}>
                                    999
                                </Col>
                                <Col style={{ paddingLeft: 0, paddingRight: 0, border: "ridge #404040", borderWidth: "0 0 0 medium" }}>
                                    <Image src={process.env.PUBLIC_URL + "/game_icons/2 simple sword.png"} rounded style={{width: "40px", height: "40px", margin: 0}}/>
                                </Col>
                                <Col style={{ paddingLeft: 0, paddingRight: 0 }}>
                                    999
                                </Col>
                                <Col style={{ paddingLeft: 0, paddingRight: 0, border: "ridge #404040", borderWidth: "0 0 0 medium" }}>
                                    <Image src={process.env.PUBLIC_URL + "/game_icons/gem.png"} rounded style={{width: "40px", height: "40px", margin: 0}}/>
                                </Col>
                                <Col style={{ paddingLeft: 0, paddingRight: 0 }}>
                                    999
                                </Col>
                            </Row>
                        </Container>
                    </div>
                </Card.Body>
            </Card>
}